::placeholder {
  color: rgba(245, 245, 245, 0.836);
}

:root {
  /* --theme-color-dark: #09184a; */
  --theme-color-dark: rgb(2, 0, 119);
  --theme-color-light: #98d6ff;
  --font-size-xl: 4rem;
  --font-size-l: 2.1rem;
  --font-size-m: 1.3rem;
  --font-size-sm: 1.1rem;
  --font-size-vsm: 0.9rem;
  --heading-font: "Fira Sans", sans-serif;
  /* --content-font: "Roboto", sans-serif; */
  /* --content-font: "Abel", sans-serif; */
  --content-font: "Noto Sans", sans-serif;

  --speed: 0.85;
  --primary: #fff;
  --size: 30;
  --ease: cubic-bezier(0.175, 0.885, 0.32, 1.275);
}

body {
  margin: 0;
  padding: 0;
  -webkit-tap-highlight-color: transparent;
  overflow-x: hidden !important;
}
.App {
  margin: 0;
}

main {
  margin-left: 78px;
}

.video-container {
  position: fixed;
  height: 100vh;
  z-index: -2;
}
.video-container video {
  min-width: 100%;
  min-height: 100%;
  width: auto;
  height: auto;
}

/* OVERRIDING THE CSS OF EXTERNAL LIBRARY */

.slider-left div,
.slider-right div {
  border: none !important;
}

.react-3d-carousel .slider-container .slider-left div,
.react-3d-carousel .slider-container .slider-right div {
  font-size: 45px;
}

.react-3d-carousel .slider-container .slider-left div:hover,
.react-3d-carousel .slider-container .slider-right div:hover {
  color: #98d6ff;
  color: var(--theme-color-light);
}

.react-3d-carousel .slider-container .slider-left {
  right: 120% !important;
}

.react-3d-carousel .slider-container .slider-right {
  left: 120% !important;
}

@media only screen and (max-width: 768px) {
  main {
    -ms-scroll-snap-type: unset;
        scroll-snap-type: unset;
    height: -webkit-max-content;
    height: max-content;
    min-height: 100vh;
    scroll-behavior: smooth;
    margin-left: unset;
  }
  section {
    height: -webkit-max-content !important;
    height: max-content !important;
  }
  .react-3d-carousel .slider-container .slider-left {
    right: 101% !important;
  }
  .react-3d-carousel .slider-container .slider-right {
    left: 101% !important;
  }
  .react-3d-carousel .slider-container .slider-left div,
  .react-3d-carousel .slider-container .slider-right div {
    font-size: 25px;
  }
}

#cp-ambd a {
  all: unset;
}
#cp-ambd{
  -webkit-tap-highlight-color: transparent;
  scroll-behavior: smooth;
  font-family: 'Noto Sans', sans-serif;
}
#cp-ambd nav {
  width: 100%;
  padding: 20px 5px;
  position: fixed;
  top: 0;
  left: 0;
  font-size: 1.5em;
  color: white;
  display: flex;
  cursor: pointer;
  flex-wrap: wrap;
  z-index: 9;
  border-bottom: 2px solid rgba(116, 192, 255, 0.466);
  transition: all 0.5s;
}
.nav-highlight{
  background-color: white !important;
  color: #000 !important;
}
#cp-ambd #logo {
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 1.3em;
  letter-spacing: 0.25em;
  margin-left: 1em;
  text-transform: uppercase;
  mask-image: linear-gradient(
    -75deg,
    rgba(0, 0, 0, 0.6) 30%,
    #000 50%,
    rgba(0, 0, 0, 0.6) 70%
  );
  -webkit-mask-image: linear-gradient(
    -75deg,
    rgba(0, 0, 0, 0.6) 30%,
    #000 50%,
    rgba(0, 0, 0, 0.6) 70%
  );
  mask-size: 200%;
  -webkit-mask-size: 200%;
  -webkit-animation: shine-ca 3s infinite;
          animation: shine-ca 3s infinite;
  font-family: atmospheric;
}
@-webkit-keyframes shine-ca {
  from {
    -webkit-mask-position: 150%;
  }

  to {
    -webkit-mask-position: -50%;
  }
}
@keyframes shine-ca {
  from {
    -webkit-mask-position: 150%;
  }

  to {
    -webkit-mask-position: -50%;
  }
}
#cp-ambd #navList {
  display: flex;
  margin-left: auto;
  margin-right: 20px;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  flex-wrap: nowrap;
  transition: height 0.5s;
}
#cp-ambd #navList div {
  margin: 0 15px;
  transition: all 0.5s;
}
#cp-ambd #navList div:hover {
  color: #98d6ff;
  -webkit-transform: scale(1.3);
          transform: scale(1.3);
}
#cp-ambd #collapseBtn {
  position: absolute;
  top: 21px;
  right: 20px;
  font-size: 1.5em;
  display: none;
}
#cp-ambd .fullHeight {
  height: auto !important;
}

#cp-ambd #home {
  width: 100%;
  height: 100%;
  background-color: #000;
  background: url(/static/media/background.d041c06f.jpg);
  background-position: center;
  background-size: cover;
  overflow: hidden;
}
#cp-ambd #home #home-mask{
  width: 100%;
  height: 100%;
  background: rgb(0,0,0);
  background: linear-gradient(90deg, rgba(0,0,0,0.4) 0%, rgba(0,0,0,0.80) 30%, rgba(0,0,0,0.80) 70%, rgba(0,0,0,0.40) 100%);
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  padding-top: 77px;
}
#cp-ambd #home #tnLogo{
    width: 300px;
    margin-bottom: 15px;
}
#cp-ambd #home #theme{
  color: white;
  font-size: 30px;
  text-transform: uppercase;
  letter-spacing: 2px;
  margin-top: 15px;
  margin-bottom: 25px;
  width: 80%;
  max-width: 900px;
  text-align: center;
}
#cp-ambd #registerbtn {
  padding: 15px 30px;
  color: #fff;
  border-radius: 5px;
  padding: 10px 25px;
  font-family: 'Lato', sans-serif;
  font-weight: 500;
  cursor: pointer;
  transition: all 0.3s ease;
  position: relative;
  display: inline-block;
   box-shadow:inset 2px 2px 2px 0px rgba(255,255,255,.5),
   7px 7px 20px 0px rgba(0,0,0,.1),
   4px 4px 5px 0px rgba(0,0,0,.1);
  outline: none;
  border: none;
  background: #0b5299;
    background: linear-gradient(0deg, #0b5299 0%, #0b5299 100%);
    color: #fff;
    overflow: hidden;
    font-size: 1.2rem;
    text-transform: uppercase;
    letter-spacing: 0.1rem;
}
#cp-ambd #registerbtn:hover {
    text-decoration: none;
    color: #fff;
}
#cp-ambd #registerbtn:before {
    position: absolute;
    content: '';
    display: inline-block;
    top: -180px;
    left: 0;
    width: 30px;
    height: 100%;
    background-color: #fff;
    -webkit-animation: shiny-btn1 3s ease-in-out infinite;
            animation: shiny-btn1 3s ease-in-out infinite;
}
#cp-ambd #registerbtn:hover{
  opacity: .7;
}
#cp-ambd #registerbtn:active{
  box-shadow:  4px 4px 6px 0 rgba(255,255,255,.3),
              -4px -4px 6px 0 rgba(116, 125, 136, .2), 
    inset -4px -4px 6px 0 rgba(255,255,255,.2),
    inset 4px 4px 6px 0 rgba(0, 0, 0, .2);
}


@-webkit-keyframes shiny-btn1 {
    0% { -webkit-transform: scale(0) rotate(45deg); opacity: 0; }
    80% { -webkit-transform: scale(0) rotate(45deg); opacity: 0.5; }
    81% { -webkit-transform: scale(4) rotate(45deg); opacity: 1; }
    100% { -webkit-transform: scale(50) rotate(45deg); opacity: 0; }
}


@keyframes shiny-btn1 {
    0% { -webkit-transform: scale(0) rotate(45deg); opacity: 0; }
    80% { -webkit-transform: scale(0) rotate(45deg); opacity: 0.5; }
    81% { -webkit-transform: scale(4) rotate(45deg); opacity: 1; }
    100% { -webkit-transform: scale(50) rotate(45deg); opacity: 0; }
}


#cp-ambd #social-links{
  color: white;
  position: fixed;
  bottom: 0px;
  left: 0px;
  padding: 20px;
}
.link-highlight{
  background-color: rgba(0, 0, 0, 0.8);
}
#cp-ambd #social-links a{
  display: block;
  margin-bottom: 20px;
  font-size: 40px;
  transition: all 0.5s;
  cursor: pointer;
  background-color: #fff;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

#cp-ambd #social-links a:nth-of-type(1):hover{
  background-color: #00acee;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}
#cp-ambd #social-links a:nth-of-type(2):hover{
  background: #f09433; 
  background: linear-gradient(45deg, #f09433 0%,#e6683c 25%,#dc2743 50%,#cc2366 75%,#bc1888 100%); 
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}
#cp-ambd #social-links a:nth-of-type(3):hover{
  background-color: #2480db;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}



#cp-ambd section{
  padding: 70px 8%;
