.sidebar {
  position: fixed;
  float: left;
  z-index: 45;
  top: 0;
  left: 0;
  height: 100vh;
  width: 260px;
  background-image: linear-gradient(rgb(13, 15, 39), rgb(19, 19, 57), blue);
  z-index: 100;
  transition: all 0.5s ease;
  border-radius: 0px 2px 2px 0;
  cursor: pointer;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.sidebar.close {
  width: 78px;
}

.sidebar .logo-details {
  height: 60px;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: start;
  padding: 18px;
  margin-bottom: 15px;
}

.sidebar .logo-details i {
  font-size: 30px;
  color: #fff;
  height: 50px;
  min-width: 78px;
  text-align: center;
  line-height: 50px;
}
.sidebar .logo-details .logo_name {
  font-size: 22px;
